body {
  margin: 0;
  padding: 0;
  background-color: #000000;

}
.page {
  min-width: 320px;
  width: 100%;
  max-width: 880px;
  box-sizing: border-box;
  position: relative;
  margin: 0 auto;
  color: #FFFFFF;
  font-family: "Inter", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  z-index: 1;
}

/* @media screen and (min-width: 768px) {
  .page {
    padding: 28px 50px 45px;
  }
  
}

@media screen and (min-width: 1280px) {
  .page {
    padding: 45px 200px 60px;
  }
} */
.page__main {
  margin: 36px auto 0;
  min-height: 75vh;
}
.list {
    list-style: none;
    padding: 0;
    margin: 0;
}
.button {
  background-color: #000000;
  transition: opacity 0.5s ease-in-out;
  color: #FFFFFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button:hover {
  opacity: 60%;
}

.button:active {
  color: white;
}


.button_white {
    background-color: #FFFFFF;
    color: #000000;
}
.hidden {
    display: none;
}
.link {
    color: white;
    text-decoration: none;
    z-index: 1;
    transition: opacity 0.5s;
    display: inline-block;
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
}

.link:hover {
    opacity: 0.8;
}


.header {
  display: flex;
  justify-content: space-between;
  padding: 28px 30px 31.6px;
  position: relative;
  border-bottom: 1px solid #545454;
}
@media screen and (min-width: 768px) {
  .header {
    padding: 45px 100px 41px;
  }
}



.header__logo {
  width: 103.74px;
  height: 24.4px;
  /*margin: 0 auto 31.6px 27px;*/
}

@media screen and (min-width: 768px) {
  .header__logo {
    width: 142px;
    height: 33px;
  }
}

.header__auth {
    display: block;
    font-family: 'Inter';
    font-style: normal;
    font-size: 14px;
    line-height: 17px;
    font-weight: 400;

    text-align: right;

    align-self: center;

    color: #A9A9A9;
}

@media screen and (min-width: 768px) {
    .header__auth {
        font-size: 18px;
        line-height: 22px;
        margin: 0 0 0 24px;
    }

}
.header__menu {
    flex-direction: row;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    align-items: center;
}

@media screen and (min-width: 768px) {
    .header__menu {
        display: flex;
    }

}
.header__menu_hidden {
    display: none;
}

@media screen and (min-width: 768px) {
    .header__menu_hidden {
        display: flex;
    }

}
.header__menu_open {
    display: flex;
    flex-direction: column;
    grid-gap: 18px;
    gap: 18px;
}

.header__email {
    margin: 0;
    font-size: 18px;
    line-height: 22px;
}
@media screen and (max-width: 767px) {
    .header__burger {
        display: block;
        width: 30px;
        margin: 0 0 0 auto;
    }
    .header__burger_hidden {
        display: none;
    }
}

.header__logo_hidden {
    display: none;
}
.header_center {
    justify-content: center;
    align-items: center;
}
.profile {
  margin: 0 auto 36px;
  padding: 0;
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .profile {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0 20px 50px;
    white-space: break-spaces;
  }
}


@media screen and (min-width: 1280px) {
  .profile {
    margin: 0 0 50px;
  }
}
.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .profile__avatar {
    margin: 0 auto;
  }
}

.profile__button_type_change-avatar:hover .profile__avatar {
  opacity: 0.2;
}
.profile__button_type_add {
  width: 88%;
  margin:  0 auto 0;
  border: 2px solid #FFFFFF;
  height: 50px;
}

@media screen and (min-width: 768px) {
  .profile__button_type_add {
    width: 150px;
    height: 50px;
  }  
}
.profile__button_type_edit {
  width: 18px;
  height: 18px;
  border: 1px solid #FFFFFF;
  align-self: center;
}

@media screen and (min-width: 768px) {
  .profile__button_type_edit {
    width: 24px;
    height: 24px;
    align-self: center;
    margin-top: 7px;
  }
}

.profile__description {
  margin: 0 auto 36px auto;
  font-size: 14px;
  line-height: 17px;
  font-style: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
