}

@media screen and (min-width: 768px) {
  .profile__info {
    align-items: baseline;
    flex-grow: 2;
    margin: 0 auto 20px;
    max-width: 50%;
  }
}
.profile__name-block {
  display: flex;
  justify-content: flex-start;
  margin: 0 auto 14px;
