}

@media screen and (min-width: 768px) {
  .profile__name-block {
    margin: 0 30px 16px 0;
  }
  
}
.profile__name {
  margin: 0 10px 0 0;
  font-size: 27px;
  line-height: 33px;
  font-weight: 500;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}


@media screen and (min-width: 768px) {
  .profile__name {
    margin: 0 18px 0 0;
    font-size: 42px;
    line-height: 48px;
  }
}
.profile__icon_type_edit-avatar {
    display: none;
    position: absolute;
}

.profile__button_type_change-avatar:hover .profile__icon_type_edit-avatar {
    display: block;
