.gallery {
  margin: 0 auto 48px;
}

@media screen and (min-width: 1280px) {
  .gallery {
    width: 100%;
    margin: 0 auto 66px;
  }
}
.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  padding: 0;
  grid-gap: 20px 17px;
  gap: 20px 17px;
  justify-content: center;
  color: #000000;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .cards {
    width: 100%;
  }
}
.cards__item {
  margin: 0 auto;
}
.card {
  width: 282px;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  background-color: #FFFFFF;
  border-radius: 10px;
  position: relative;
}

.card__body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 22px 20px 22px 21px;
}
.card__button {
  background-size: contain;
  background-position: center;
  border: none;
  background-color: transparent;
  align-self: center;
  background-repeat: no-repeat;
  transition: opacity 0.5s ease-in-out;
  cursor: pointer;
}

.card__button:hover {
  opacity: 0.5;
}
.card__like-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.card__like-counter {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-align: center;

  color: #000000;
  margin: 0;

}

.card__like-button {
  width: 22px;
  height: 19px;
}


.card__like-button_active {
  background-image: url(/react-mesto-auth/static/media/like_active.3d813f81.svg);
}
.card__like-button_inactive {
    background-image: url(/react-mesto-auth/static/media/heart-vector.00b1c22a.svg);
}
.card__img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  object-fit: cover;
  object-position: center;
  background: #c4c4c4;
  height: 282px;
}
.card__delete-button {
  background-image: url(/react-mesto-auth/static/media/delete.c25e9417.svg);

  top: 20px;
  right: 20px;
  width: 18px;
  height: 19px;
}
.card__delete-button_hidden {
    display: none;
}
.card__delete-button_visible {
    position: absolute;
}
.card__title {
  max-width: 90%;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 24px;
  line-height: 35px;
  font-weight: 900;
}
.footer {
  color: #545454;
  margin: 0 auto 36px;
  padding: 0 30px;
}

@media screen and (min-width: 768px) {
  .footer {
    margin: 0 auto 60px;
    padding: 0 100px;
  }
