.popup {
  visibility: hidden;
  opacity: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.5);
  transition: visibility 0.5s, opacity 0.5s linear;
}



.popup_auth {
    position: relative;
    z-index: -1;
}
.popup_open {
  visibility: visible;
  opacity: 1;
}
.popup__container {
  width: 88%;
  max-width: 330px;

  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  color: #000000;
  z-index: 1;
  position: relative;
  justify-content: center;
  align-items: center;
}
