  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 75vw;
  max-height: 75vh;
  background-color: transparent;
  padding: 0;
}
.popup__container_infoTooltip {
    padding: 50px 18px;
}
@media screen and (min-width: 768px) {
    .popup__container_infoTooltip {
        padding: 60px 36px;
    }
}
